const setDIR = n => {
    let dir =
        n > 348.75 || n <= 11.25
            ? 'N'
            : n > 11.25 && n <= 33.75
                ? 'NNE'
                : n > 33.75 && n <= 56.25
                    ? 'NE'
                    : n > 56.25 && n <= 78.75
                        ? 'ENE'
                        : n > 78.75 && n <= 101.25
                            ? 'E'
                            : n > 101.25 && n <= 123.75
                                ? 'ESE'
                                : n > 123.75 && n <= 146.25
                                    ? 'SE'
                                    : n > 146.25 && n <= 168.75
                                        ? 'SSE'
                                        : n > 168.75 && n <= 191.25
                                            ? 'S'
                                            : n > 191.25 && n <= 213.75
                                                ? 'SSW'
                                                : n > 213.75 && n <= 236.25
                                                    ? 'SW'
                                                    : n > 235.25 && n <= 258.75
                                                        ? 'WSW'
                                                        : n > 258.75 && n <= 281.25
                                                            ? 'W'
                                                            : n > 281.25 && n <= 303.75
                                                                ? 'WNW'
                                                                : n > 303.75 && n <= 326.25
                                                                    ? 'NW'
                                                                    : 'NNW';
    return dir;
};

export default setDIR;



